.track-slide {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
  position: relative;
  overflow: hidden;
  padding: 2rem;
}

.track-slide::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 20% 30%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(59, 130, 246, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.track-content {
  max-width: 1200px;
  width: 100%;
  height: 100%;
  max-height: calc(100vh - 4rem);
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}

.track-header-wrapper {
  position: relative;
  width: 100%;
}

.track-header {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: 100%;
}

.track-play-button {
  position: absolute;
  top: 0;
  right: 2rem;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid rgba(139, 92, 246, 0.3);
  background: rgba(139, 92, 246, 0.1);
  color: #a78bfa;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  padding-left: 3px;
  backdrop-filter: blur(10px);
}

.track-play-button:hover {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.5);
  transform: scale(1.05);
}

.track-play-button:active {
  transform: scale(0.95);
}

.artist-name {
  font-size: clamp(2rem, 5vw, 3.5rem);
  font-weight: 300;
  letter-spacing: 0.15em;
  margin: 0;
  color: #f5f5f5;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.header-divider {
  height: 1px;
  width: 0;
  background: linear-gradient(90deg, #8b5cf6, #a78bfa);
  animation: growHeaderLine 1s ease-out forwards;
}

@keyframes growHeaderLine {
  to {
    width: 120px;
  }
}

.artist-subtitle {
  font-size: clamp(0.875rem, 1.5vw, 1.125rem);
  letter-spacing: 0.3em;
  color: #a0a0a0;
  text-transform: uppercase;
  margin: 0;
  font-weight: 300;
}

.track-main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: center;
}

.album-art {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  border-radius: 8px;
  overflow: hidden;
  box-shadow:
    0 20px 60px rgba(0, 0, 0, 0.5),
    0 0 60px rgba(139, 92, 246, 0.5),
    0 0 100px rgba(139, 92, 246, 0.3),
    0 0 140px rgba(139, 92, 246, 0.15);
}

.album-art img {
  width: 100%;
  height: auto;
  display: block;
}

.track-info {
  text-align: left;
  padding: 2rem;
}

.track-title {
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: 300;
  letter-spacing: 0.05em;
  margin: 0 0 0.5rem 0;
  color: #f5f5f5;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.track-subtitle {
  font-size: clamp(0.875rem, 1.2vw, 1rem);
  color: #D4AF37;
  font-weight: 700;
  letter-spacing: 0.05em;
  margin: 0 0 0.5rem 0;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.track-description {
  font-size: clamp(1rem, 1.5vw, 1.25rem);
  line-height: 1.8;
  color: #c0c0c0;
  margin: 0;
  font-weight: 300;
}

.platforms-inline {
  display: flex;
  gap: 1rem;
  align-items: flex-end;
  margin: 0 0 1rem 0;
  flex-wrap: wrap;
}

.platform-button-inline {
  padding: 0.25rem;
  background: transparent;
  border: none;
  text-decoration: none;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  gap: 0.35rem;
  min-height: 80px;
}

.platform-button-inline:hover {
  transform: scale(1.05);
}

.platform-name {
  font-size: 0.75rem;
  color: #a0a0a0;
  text-align: center;
  font-weight: 400;
  letter-spacing: 0.02em;
  white-space: nowrap;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
}

.platform-logo {
  height: 32px;
  width: 32px;
  object-fit: contain;
  filter: brightness(1.1) saturate(1.2);
}

.medium-logo {
  height: 42px;
  width: 42px;
}

.apple-logo {
  height: 44px;
  width: 44px;
  object-fit: contain;
}

.tall-logo {
  height: 49px;
  width: 57px;
  object-fit: cover;
  object-position: top;
}

.large-logo {
  height: 53px;
  width: 58px;
  object-fit: cover;
  object-position: top;
}

.platform-text {
  font-size: 0.65rem;
  font-weight: 500;
  color: #a0a0a0;
  text-align: center;
  letter-spacing: 0.05em;
  line-height: 1.2;
}

.platform-button-inline:hover .platform-logo {
  filter: brightness(1.3) saturate(1.3);
}

.platform-button-inline:hover .platform-name {
  color: #c0c0c0;
}


@media (max-width: 968px) {
  .track-slide {
    padding: 1rem;
  }

  .track-content {
    gap: 2rem;
  }

  .track-header {
    gap: 0.75rem;
  }

  .track-play-button {
    right: 1rem;
    width: 44px;
    height: 44px;
    font-size: 1rem;
  }

  .header-divider {
    animation: growHeaderLineMobile 1s ease-out forwards;
  }

  @keyframes growHeaderLineMobile {
    to {
      width: 80px;
    }
  }

  .track-main {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .track-info {
    text-align: center;
    padding: 1rem;
  }

  .platforms-grid {
    gap: 0.75rem;
  }

  .platform-button {
    padding: 0.5rem;
  }

  .platform-name {
    font-size: 0.7rem;
  }

  .platform-logo {
    height: 28px;
    width: 28px;
  }

  .medium-logo {
    height: 36px;
    width: 36px;
  }

  .apple-logo {
    height: 38px;
    width: 38px;
  }

  .tall-logo {
    height: 42px;
    width: 50px;
  }

  .large-logo {
    height: 55px;
    width: 50px;
  }
}

@media (max-height: 700px) {
  .track-content {
    gap: 1rem;
    max-height: calc(100vh - 2rem);
  }

  .track-header {
    gap: 0.5rem;
  }

  .artist-name {
    font-size: clamp(1.5rem, 4vw, 2.5rem);
  }

  .track-main {
    gap: 1.5rem;
  }

  .album-art {
    max-width: 350px;
  }

  .track-title {
    font-size: clamp(1.5rem, 3vw, 2rem);
    margin-bottom: 1rem;
  }

  .track-description {
    font-size: clamp(0.9rem, 1.3vw, 1.1rem);
  }

  .platforms-label {
    margin-bottom: 1rem;
  }

  .platforms-grid {
    gap: 0.75rem;
  }

  .platform-logo {
    height: 24px;
    width: 24px;
  }

  .medium-logo {
    height: 32px;
    width: 32px;
  }

  .large-logo {
    height: 36px;
    width: 44px;
  }
}
