.header-banner {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 150;
  background: rgba(10, 10, 10, 0.95);
  backdrop-filter: blur(10px);
  padding: 1rem 2rem;
  border-bottom: 1px solid rgba(139, 92, 246, 0.2);
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1400px;
  margin: 0 auto;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}

.banner-content {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.banner-name {
  font-size: 1.25rem;
  font-weight: 300;
  letter-spacing: 0.15em;
  margin: 0;
  color: #f5f5f5;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.banner-divider-vertical {
  width: 1px;
  height: 0;
  background: linear-gradient(180deg, #8b5cf6, #a78bfa);
  animation: growLineVertical 1.5s ease-out forwards;
}

@keyframes growLineVertical {
  to {
    height: 30px;
  }
}

.banner-subtitle {
  font-size: 0.75rem;
  letter-spacing: 0.2em;
  color: #a0a0a0;
  text-transform: uppercase;
  margin: 0;
  font-weight: 300;
}

.player-control {
  display: flex;
  align-items: center;
}

.play-button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid rgba(139, 92, 246, 0.3);
  background: rgba(139, 92, 246, 0.1);
  color: #a78bfa;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  padding-left: 2px;
}

.play-button:hover {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.5);
  transform: scale(1.05);
}

.play-button:active {
  transform: scale(0.95);
}

@media (max-width: 768px) {
  .header-banner {
    padding: 0.75rem 1rem;
  }

  .banner-content {
    gap: 0.75rem;
  }

  .banner-name {
    font-size: 1rem;
  }

  .banner-divider-vertical {
    animation: growLineVerticalMobile 1.5s ease-out forwards;
  }

  @keyframes growLineVerticalMobile {
    to {
      height: 24px;
    }
  }

  .banner-subtitle {
    font-size: 0.65rem;
  }

  .play-button {
    width: 36px;
    height: 36px;
    font-size: 0.875rem;
  }
}

@media (max-width: 480px) {
  .header-banner {
    padding: 0.5rem 0.75rem;
  }

  .banner-content {
    gap: 0.5rem;
  }

  .banner-name {
    font-size: 0.875rem;
    letter-spacing: 0.1em;
  }

  .banner-divider-vertical {
    animation: growLineVerticalSmall 0.8s ease-out forwards;
  }

  @keyframes growLineVerticalSmall {
    to {
      height: 20px;
    }
  }

  .banner-subtitle {
    font-size: 0.6rem;
  }

  .play-button {
    width: 32px;
    height: 32px;
    font-size: 0.75rem;
  }
}

@media (max-height: 700px) {
  .header-banner {
    padding: 0.5rem 1.5rem;
  }

  .banner-name {
    font-size: 1rem;
  }

  .banner-subtitle {
    font-size: 0.65rem;
  }
}
