.world-record-slide {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
  position: relative;
  overflow: hidden;
  padding: 0 2rem;
  padding-top: 70px;
}

.world-record-slide::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(circle at 50% 50%, rgba(139, 92, 246, 0.15) 0%, transparent 60%);
  pointer-events: none;
}

.record-content {
  max-width: 1200px;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.record-layout {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  width: 100%;
}

.label-text {
  display: inline-block;
  padding: 0.75rem 2rem;
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.3);
  color: #a78bfa;
  font-size: 0.875rem;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  border-radius: 30px;
  font-weight: 400;
}

.record-title {
  font-size: clamp(1.75rem, 3vw, 2.5rem);
  font-weight: 300;
  letter-spacing: 0.03em;
  margin: 0 0 1.5rem 0;
  color: #f5f5f5;
  font-family: 'Cinzel', 'Playfair Display', serif;
  line-height: 1.3;
}

.record-title-group {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.record-title-compact {
  font-size: clamp(1.2rem, 2vw, 1.6rem);
  margin: 0 0 0.5rem 0;
  font-weight: 700;
  line-height: 1.3;
}

.record-subtitle-wrapper {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.gbwr-logo {
  height: 2rem;
  width: auto;
  object-fit: contain;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.record-subtitle-wrapper a:hover .gbwr-logo {
  transform: scale(1.05);
  opacity: 1;
}

.record-subtitle {
  font-size: clamp(1.05rem, 1.44vw, 1.2rem);
  color: #D4AF37;
  font-weight: 700;
  letter-spacing: 0.05em;
  margin: 0;
  white-space: nowrap;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.record-hero {
  width: 100%;
}

.hero-placeholder {
  width: 100%;
  aspect-ratio: 16 / 9;
  background: rgba(26, 26, 26, 0.5);
  border: 2px solid rgba(139, 92, 246, 0.2);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  overflow: hidden;
}

.hero-placeholder img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-image {
  width: 100%;
  border-radius: 12px;
  overflow: hidden;
  box-shadow:
    0 20px 60px rgba(0, 0, 0, 0.5),
    0 0 60px rgba(139, 92, 246, 0.5),
    0 0 100px rgba(139, 92, 246, 0.3),
    0 0 140px rgba(139, 92, 246, 0.15);
}

.hero-image img {
  width: 100%;
  height: auto;
  display: block;
}

.placeholder-icon {
  font-size: 6rem;
  opacity: 0.3;
}

.record-details {
  width: 100%;
  text-align: left;
  padding: 2rem;
}

.detail-item {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 2rem;
}

.detail-label {
  font-size: 0.875rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #808080;
  font-weight: 300;
}

.detail-value {
  font-size: clamp(1.5rem, 3vw, 2rem);
  color: #8b5cf6;
  font-weight: 400;
  font-family: 'Cinzel', 'Playfair Display', serif;
}

.detail-divider {
  height: 1px;
  width: 100px;
  background: linear-gradient(90deg, #8b5cf6, transparent);
  margin: 1.5rem 0;
}

.record-description {
  font-size: clamp(1rem, 1.5vw, 1.25rem);
  line-height: 1.8;
  color: #c0c0c0;
  margin: 0;
  font-weight: 300;
  text-align: left;
}

.record-description strong {
  color: #f5f5f5;
  font-weight: 600;
}

.record-details {
  position: relative;
}

.subtitle-with-icon {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.social-link-inline {
  display: inline-flex;
  align-items: center;
  transition: transform 0.3s ease;
}

.social-link-inline:hover {
  transform: scale(1.1);
}

.social-icon {
  width: 24px;
  height: 24px;
  object-fit: contain;
  opacity: 0.7;
}

.social-link-inline:hover .social-icon {
  opacity: 1;
}

.emoji-icon-inline {
  font-size: 1.5rem;
  opacity: 0.8;
  display: inline-block;
}

@media (max-width: 968px) {
  .world-record-slide {
    padding: 1rem;
  }

  .record-content {
    gap: 2rem;
  }

  .record-layout {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .record-details {
    padding: 1rem;
    text-align: center;
  }

  .record-description {
    text-align: center;
  }

  .detail-divider {
    margin: 2rem auto;
    background: linear-gradient(90deg, transparent, #8b5cf6, transparent);
  }

  .placeholder-icon {
    font-size: 4rem;
  }

  .gbwr-logo {
    height: 1.5rem;
  }

  .record-subtitle-wrapper {
    gap: 0.5rem;
  }
}

@media (max-height: 700px) {
  .world-record-slide {
    padding-top: 70px;
  }

  .record-content {
    max-height: calc(100vh - 80px);
  }

  .record-layout {
    gap: 2rem;
  }

  .record-title {
    font-size: clamp(1.5rem, 2.5vw, 2rem);
  }

  .record-title-compact {
    font-size: clamp(1rem, 1.8vw, 1.4rem);
  }

  .record-subtitle {
    font-size: clamp(0.8rem, 1.2vw, 0.95rem);
  }

  .gbwr-logo {
    height: 1.5rem;
  }

  .detail-value {
    font-size: clamp(1.2rem, 2.5vw, 1.6rem);
  }

  .record-description {
    font-size: clamp(0.9rem, 1.3vw, 1.1rem);
    line-height: 1.6;
  }

  .hero-image img {
    max-height: 400px;
    object-fit: cover;
  }

  .record-details {
    padding: 1rem;
  }

  .social-icon {
    width: 28px;
    height: 28px;
  }
}

@media (max-width: 480px) {
  .world-record-slide {
    padding: 0.5rem 1rem;
    padding-top: 65px;
  }

  .record-layout {
    gap: 1.5rem;
  }

  .record-title-compact {
    font-size: clamp(1rem, 1.8vw, 1.3rem);
  }

  .record-subtitle {
    font-size: clamp(0.85rem, 1.3vw, 1rem);
  }

  .gbwr-logo {
    height: 1.25rem;
  }

  .record-description {
    font-size: clamp(0.875rem, 1.2vw, 1rem);
  }
}
